
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #ffffff;
    z-index: 100;
}
.image{
    width: 210px;
    margin: 0px 0 0 15px;
}
.linkbutton{
    text-decoration: none;
    color: #000000;
}
.linkbutton1{
    text-decoration: none;
    color: #000000;
    background-color: #b7b7b7;
}
.linkbutton:hover{
    color: #f4671f;
}
.navigation{
    width: 100%;
    height: 50px;
}
.navigation ul{
    display: flex;
    height: 50px;
    width: 100%;
    margin: 0px;
    padding: 0px;
    overflow: hidden;
    float:10;
    font-size: 20px;
    
}
/*need to style this*/
.navigation ul li{
    list-style-type: none;
    text-align: center;
    margin: auto;    
}
.hamburger{
    z-index: 6;
} 
.color{
    color: #4e60ff;
}

@media all and (max-width: 700px) {
    .hamburger{
        display:flex;
        padding-top: 10px;
        margin-left: 10px;
        z-index: 6;
    }
    .navigation ul{
        background-color: #ffffff;
        height: 100vh;
        width: 100vw;
        margin-top: 50px;
        position: absolute;
        right: 0;
        
    }
    .navigation{
        max-width: 80px;
    }
    .navigation ul li{
        font-size: 25px;
        padding:30px; 
        border-style: none none solid none;   
        border-color: rgb(230, 230, 230);
    }
}